module.exports = [{"title":"Senior Site Reliability Engineer","company":"GE Digital","start":"February 2019","end":"","logo":"general_electric.png","description":"With my great track record of taking over failing projects and digitally transforming them, I was able to take on more responsibilities. In this role, I created an internal event pipeline for monitoring alerts. The pipeline correlated alerts across all GE infrastructure and reduced operational noise immensely. I created it to allow total configurability of alerts, customized noise suppression, and even included machine learning to predict root causes of incidents. My goal in this role is to become an industry leader in the monitoring/aiops space."},{"title":"Lifecycle Operations Engineer","company":"GE Digital","start":"December 2017","end":"February 2019","logo":"general_electric.png","description":"After I felt like I learned all that I could, I looked to expand my skill set. In this role I was first introduced to monitoring and metrics. I focused efforts to automate mundane tasks that were common and shaved off days worth of work into a few minutes of time. While still learning the systems, I refactored many codebases to be more readable, less redundant and optimized. I took the monitoring platform availability from ~80% up to 99.99998%. I learned a great deal on debugging solaris, linux and windows. This is where I realized the importance of creating software that doesn't consume more resources than allocated on its underlying infrastructure."},{"title":"Software Engineer","company":"Utility Associates, Inc.","start":"May 2015","end":"December 2017","logo":"utility.png","description":"I was fortunate to land a role in at this company. They gave me a shot, and I did not disappoint. In this role, I was able to get hands-on experience with full-stack development. I was able to soak up knowledge from strong lead engineers and was able to grow exponentially. I wore many hats including refactoring code, developing features, optimizing database queries, and designing friendly front-end user experiences. Every morning, I came into work with a mindset of learning something new. I'd often be the first into the office, and long after everyone left, I'd always be the last to leave."},{"title":"Programming/IT Tutor","company":"Georgia Gwinnett College","start":"August 2013","end":"May 2015","logo":"georgia_gwinnett.png","description":"I was hand-picked by a professor who saw how fast I was able to pick up java and help other students learn in classes. I often held programming session with classmates, so this job aligned with what I was already doing. In this role, I learned the ins and outs of fundamental object-oriented programming. Java became my life, and I spent 20 hours a week experimenting with new concepts and creating random software to use in my day-to-day life. I became proficient enough in all areas in java programming and taught lessons in classes as the TA for a couple of professors."}]

