module.exports = {"name":"Landon Dao","position":"Software Engineer","development_start":"May 2015","images":[{"name":"landon_einstein.jpg","text":"Talking to a friend"},{"name":"landon_rooftop.jpg","text":"Close Up!"},{"name":"landon_mountains.jpg","text":"Tôi ở Phong Nha"}],"summary":"I am a technology generalist. My desired job description is something that is difficult and challenging. I love to grow and surpass expectations.","text":"I have a passion for learning new technologies and developing my own skill set. I recently graduated from Georgia Tech with a masters in computer science. I am attracted towards full stack development and devops because I like knowing exactly what to do when there is anything wrong with my system. I think it is important to understand what side effects application software might have on a server. My style of development tends to focus on optimization and maintainability. I add tracing to my code to make metrics like five 9's easily visible from a back-end perspective. I use a combination of object-oriented and functional programming to make my code cleaner. Every day I try to push the limits of my knowledge and capabilities."}

